  color: #ffffff;
}

.link-f:hover {
  color: #fff;
}

.link-f:hover::before {
  border-color: #fff;
}

.link-f::before {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px dashed #ffffff;
  transition: border 0.2s linear;
}

.link-more.collapsed::before {
