}

.link-image:hover {
  opacity: 0.8;
}

.link-opacity {
  display: block;
  color: inherit;
  opacity: 0.7;
  transition: opacity 0.2s linear;
}

.link-opacity:hover {
  color: inherit;
  opacity: 1;
}

.link-title {
  color: inherit;
}

.link-title:hover {
  color: #00ff4c;
}

.link-line {
  color: #75fa49;
  text-decoration: underline;
}

.link-line:hover {
  color: #00ff4c;
  text-decoration: underline;
}

.link-icon:hover .small-icon .si-img .sii-hover {
  opacity: 1;
}

.link-icon:hover .small-icon .si-text {
  color: #00ff4c;
}

.link-icon .small-icon .si-img {
  position: relative;
  margin-right: 0.2em;
  background-size: 1em 1em;
  background-position: left center;
  transition: opacity 0.2s linear;
}

.link-icon .small-icon .si-img .sii-hover {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-position: left center;
  background-size: 1em 1em;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.2s linear;
}

.link-icon .small-icon .si-text {
  color: #75fa49;
  transition: color 0.2s linear;
}

.link-icon .small-icon .si-text + .si-img {
  margin-right: 0;
  margin-left: 0.2em;
  background-position: right center;
}

.link-icon .small-icon .si-text + .si-img .sii-hover {
  background-position: right center;
}

a.small-icon {
