/* Slick switches */

.slick-switches {
  display: flex;
}

.slick-switches .ss-switch {
  position: relative;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 30px 30px;
  cursor: pointer;
  transition: opacity 0.2s linear;
