/* =================================================================
    Custom check/radio boxes
  ================================================================= */

.no-labels .custom-checkbox.checkbox-card label {
  display: flex !important;
  flex-direction: column;
  width: 100%;
}

.box-label {
  display: block;
  cursor: pointer;
  line-height: 1.33333;
  font-size: 16px;
  font-weight: 400;
  color: #1f1f1f;
  margin-bottom: 0;
}

.box-label .bl-type {
  line-height: 1;
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  color: #75fa49;
  transition: color 0.2s linear;
}

.bolder .box-label {
  font-weight: 700;
}

.custom-checkbox input[type='checkbox'],
.custom-checkbox input[type='radio'] {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.custom-checkbox input[type='checkbox'] + label,
.custom-checkbox input[type='radio'] + label {
  position: relative;
  padding: 0 0 0 30px;
  margin-bottom: 0;
  user-select: none;
  cursor: pointer;
}

.custom-checkbox input[type='checkbox'] + label:before,
.custom-checkbox input[type='radio'] + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 1px solid rgba(31, 31, 31, 0.15);
  border-radius: 3px;
  background: #fff;
  text-align: center;
  transition: all 0.2s linear;
}

.custom-checkbox input[type='checkbox'] + label:after,
.custom-checkbox input[type='radio'] + label:after {
  content: '';
  position: absolute;
  transform: scale(0);
  transition: all 0.2s linear;
}

.custom-checkbox input[type='checkbox'] + label:hover:before,
.custom-checkbox input[type='radio'] + label:hover:before {
  border: 1px solid rgba(0, 0, 0, 0.15);
}

.custom-checkbox input[type='checkbox'] + label:after {
  background-color: transparent;
  content: '';
  display: block;
  position: absolute;
  left: 7px;
  top: 5px;
  width: 6px;
  height: 10px;
  opacity: 0;
  border-bottom: 2px solid #1f1f1f;
  border-right: 2px solid #1f1f1f;
  transform: rotate(45deg);
  transition: all 0.2s linear;
}

.custom-checkbox input[type='checkbox']:checked + label:before {
  border-color: rgba(0, 0, 0, 0.15);
  background-color: #fff;
  content: '';
}

.custom-checkbox input[type='checkbox']:checked + label:after {
  content: '';
  opacity: 1;
}

.custom-checkbox input[type='radio'] + label:before,
.custom-checkbox input[type='radio'] + label:after {
  border-radius: 50%;
}

.custom-checkbox input[type='radio'] + label:after {
  left: 4px;
  top: 4px;
  width: 12px;
  height: 12px;
}

.custom-checkbox input[type='radio']:checked + label:before {
  border-color: #75fa49;
  animation: borderscale 300ms ease-in;
}

.custom-checkbox input[type='radio']:checked + label:after {
  background: #75fa49;
  transform: scale(1);
}

.custom-checkbox.radio-card input[type='radio'] + label {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2px 8px;
  min-width: 40px;
  min-height: 30px;
  border-radius: 5px;
  border: 1px solid #f5f5f5;
  background-color: #fff;
  text-align: center;
  transition: border 0.2s linear, background-color 0.2s linear;
}

.custom-checkbox.radio-card input[type='radio'] + label::before,
.custom-checkbox.radio-card input[type='radio'] + label::after {
  display: none !important;
}

.custom-checkbox.radio-card input[type='radio'] + label:hover {
  border-color: #75fa49;
}

.custom-checkbox.radio-card input[type='radio']:checked + label {
  border-color: #75fa49;
  background-color: #75fa49;
}

.custom-checkbox.radio-card input[type='radio']:checked + label .bl-type {
  color: #fff;
}

.custom-checkbox.radio-card input[type='radio']:disabled + label {
  position: relative;
  overflow: hidden;
  border-color: #f5f5f5;
  background-color: #fff;
}

.custom-checkbox.radio-card input[type='radio']:disabled + label::before {
  position: absolute;
  content: '';
  display: block !important;
  top: 0;
  right: 5px;
  width: 100%;
  height: 100%;
  border: none !important;
  background-color: transparent !important;
  background-image: url("../images/icons/line.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.custom-checkbox.radio-card input[type='radio']:disabled + label .bl-type {
  color: #f5f5f5;
}

